.formWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.filterLabel {
  font-family: sans-serif;
  font-size: 20px;
  font-weight: 600;
  margin-bottom: 20px;
}

.filterInput {
  font-family: sans-serif;
  display: block;
  margin: 10px 0;
  padding: 10px 20px;
  border: 1px solid brown;
  outline: none;
  border-radius: 5px;
}
